body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #eeeded;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.categoria {
  padding: 15px 0;
  border-top: 2px solid #000;
  border-bottom: 2px solid #000;
}
.verPost{
  border-bottom: 2px solid #000;
}
button:hover {
  cursor: pointer;
}
form div{
  margin: 0 auto;
}
.ver-titulo{
  background: #fff;
  border-radius: 4px;
}
.ver-contenido{
  background: #fff;
  border-radius: 4px;
  height: 150px;
}
.ver-post{
  margin: 0 auto;
}
footer {
  /* margin-top: 50px; */
  padding: 10px;
  background: #3c6a9f;
}
footer p {
  color: #fff;}
textarea{
  min-height: 150px;
}
.post{
  border-bottom: 1px solid rgb(192, 190, 190);
}